
.wrapper {
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-template-rows: 200px 1fr;
    grid-template-areas:
    "photo profileInfo"
    "photoSelect profileInfo"
    "status profileInfo";
}

.photo {
    grid-area: photo;
    padding: 10px;
}

.status{
    grid-area: status;
    padding: 10px;
}

.photoSelect {
    grid-area: photoSelect;
    padding: 10px;
}

.profileInfo {
    grid-area: profileInfo;
    padding: 10px;

}

.mainPhoto{
    max-width: 200px;
}
.contact{
    padding-left: 10px;
}
